.modal-container {
  background: rgba(50, 50, 50, 0.5);
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;

  h3 {
    font-size: 20px;
    line-height: 28px;
    font-weight: 500;
  }

  h4 {
    font-size: 16px;
    font-weight: 500;
    margin-top: 6px;
    margin-bottom: 0px;
  }
}

.modal-container.modal-open {
  text-align: center;
  -js-display: flex;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background: white;
  border-radius: 8px;
  opacity: 0;
  position: relative;
  transition: 0.6s;
}

.modal > div {
  position: relative;
  width: 100%;
  height: 100%;
  white-space: nowrap;
}

.modal-open > .modal {
  transform: translateZ(0);
  -webkit-animation: fade-in-down 0.4s both;
  animation: fade-in-down 0.4s both;
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}

#modal-close {
  cursor: pointer;
  font-size: 24px;
  padding: 12px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
}

#modal-close-dark {
  cursor: pointer;
  display: none;
  font-size: 24px;
  padding: 12px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
}

.modal-content {
  border-radius: 8px;
  display: none;
  text-align: left;
  vertical-align: top;
  white-space: normal;
}

.modal-content.active {
  display: inline-block;
  -webkit-animation: fade-in 0.4s both;
  animation: fade-in 0.4s both;
}

#modal-merchant-form {
  display: block;
}

#merchant-loading {
  color: #808080;
  display: none;
  padding: 24px 24px 0 24px;
  text-align: center;
}

#merchant-loading.active {
  -webkit-animation: fade-in-down 0.4s both;
  animation: fade-in-down 0.4s both;
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
  display: block;
}

#merchant-matches {
  display: none;
}

.modal-back {
  cursor: pointer;
  position: absolute;
  top: 16px;
  left: 14px;
  color: white;
}

.modal-back.black {
  color: #30373b;
}

.modal-back:hover .fa {
  margin-right: 4px;
}

.modal-back .fa {
  font-size: 15px;
  margin-right: 2px;
  transition: 0.2s ease;
}

.modal-form-back {
  cursor: pointer;
}

.modal-form-back:hover .fa {
  margin-right: 4px;
}

.modal-form-back .fa {
  font-size: 15px;
  margin-right: 2px;
  transition: 0.2s ease;
}

