.hero-screen-showcase {
  position: relative;
  background: #133247;
}

.showcase-images {
  position: relative;
  display: inline-block;
  height: 526px;
  width: 491px;

  @media screen and (max-width: 768px) {
    margin-top: 5px;
    width: 270px;
    zoom: 80%;

    .showcase-app-aside {
      display: none;
    }
  }
}

.order-lg-1 .showcase-images {
  float: right;

  @media screen and (max-width: 1190px) {
    float: none;
    margin: 30px auto;
    display: block;
  }

  @media screen and (max-width: 768px) {
    margin-top: 5px;
  }
}

.order-lg-2 .showcase-images {
  float: left;

  @media screen and (max-width: 1190px) {
    float: none;
    margin: 30px auto;
    display: block;
  }

  @media screen and (max-width: 768px) {
    margin: 10px auto;
  }
}

.showcase-terminal-frame {
  background-image: url('https://d85ecz8votkqa.cloudfront.net/images/poyntdotcom/showcase/terminal.png');
  background-position: left;
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  height: 520px;
  width: 270px;
  position: relative;
  z-index: 3;
}

.showcase-app {
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border-radius: 1px;
  opacity: 0.95;
  height: 375px;
  width: 221px;
  position: absolute;
  left: 25px;
  top: 123px;
  z-index: 2;
}

.showcase-app-aside {
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border-radius: 1px;
  box-shadow: 0 8px 16px 0 rgba(50, 50, 93, 0.14);
  opacity: 0.85;
  height: 375px;
  width: 221px;
  position: absolute;
  left: 269px;
  top: 123px;
}

.showcase-text {
  background: white;
  padding-left: 40px;
  padding-right: 40px;

  img {
    min-width: 200px;
    max-width: 250px;
    max-height: 80px;
  }

  h2 {
    font-size: 32px;
    line-height: 40px;
  }

  @media screen and (max-width: 768px) {
    img {
      width: 150px;
    }

    h2 {
      margin-bottom: 20px;
    }

    p, li {
      font-size: 15px;
      line-height: 22px;
    }
  }

  a {
    color: #1E9AF7;
    font-weight: 500;
  }
}

.bg-light-blue .showcase-text {
  background: #EAF0F3;
}
