.blog-content {
  .item {
    margin-bottom: 20px;
  }

  .blog-post {
    box-shadow: 0 4px 11px 0 rgba(0, 0, 0, 0.2);
    border-radius: 6px;
    padding: 0 0 40px;
    height: 100%;
    display: flex;
    flex-direction: column;

    .blog-post-image {
      height: 200px;
      border-radius: 6px 6px 0 0;
      display: block;
      background-size: cover;
      background-position: center;
    }

    h4 {
      padding: 20px 20px 0px 20px;
    }

    p {
      padding: 20px 20px 0;
      font-size: 17px;
      line-height: 24px;
    }

    .learn-more-link {
      font-size: 16px;
      padding: 20px 20px 0;
      margin-top: auto;

      button {
        padding: 0;
      }
    }
  }
}

.pagination {
  font-weight: 600;
  margin-bottom: 80px;
  position: relative;
  text-align: center;

  @media screen and (max-width: 768px) {
    padding-bottom: 30px;
  }

  .page-number {
    margin: 0 auto;
  }

  .older-posts {
    color: #0d9ded;
    position: absolute;
    right: 0;
  }

  .newer-posts {
    color: #0d9ded;
    position: absolute;
    left: 0;
  }
}
