/* Reset
/* ---------------------------------------------------------- */

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-spacing: 0;
  border-collapse: collapse;
}
img {
  max-width: 100%;
}
html {
  box-sizing: border-box;
  font-family: sans-serif;

  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
a {
  background-color: transparent;
}
a:active,
a:hover {
  outline: 0;
}
b,
strong {
  font-weight: bold;
}
i,
em,
dfn {
  font-style: italic;
}
h1 {
  margin: 0.67em 0;
  font-size: 2em;
}
small {
  font-size: 80%;
}
sub,
sup {
  position: relative;
  font-size: 75%;
  line-height: 0;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
mark {
  background-color: #fdffb6;
}
code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  margin: 0; /* 3 */
  color: inherit; /* 1 */
  font: inherit; /* 2 */
}
button {
  overflow: visible;
  border: none;
}
button,
select {
  text-transform: none;
  outline: none;
}
button,
html input[type="button"],
  /* 1 */
input[type="reset"],
input[type="submit"] {
  cursor: pointer; /* 3 */

  -webkit-appearance: button; /* 2 */
}
button[disabled],
html input[disabled] {
  cursor: default;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  padding: 0;
  border: 0;
}
input {
  line-height: normal;
}
input:focus {
  outline: none;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box; /* 1 */
  padding: 0; /* 2 */
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
input[type="search"] {
  box-sizing: content-box; /* 2 */

  -webkit-appearance: textfield; /* 1 */
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
legend {
  padding: 0; /* 2 */
  border: 0; /* 1 */
}
textarea {
  overflow: auto;
}
table {
  border-spacing: 0;
  border-collapse: collapse;
}
td,
th {
  padding: 0;
}

/* ==========================================================================
   Base styles: opinionated defaults
   ========================================================================== */

html {
  overflow-x: hidden;
  overflow-y: scroll;
  font-size: 62.5%;

  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
body {
  font-family: objektiv-mk2, sans-serif, 'Avenir Next W02','Avenir Next','Proxima Nova','Myriad Pro',Helvetica,Calibri,sans-serif;
  font-size: 1.6rem;
  line-height: 1.6em;
  font-weight: 400;
  font-style: normal;
  letter-spacing: 0;
  text-rendering: optimizeLegibility;
  background: #fff;
  padding-top: 76px;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -moz-font-feature-settings: "liga" on;
}

::selection {
  text-shadow: none;
}

hr {
  position: relative;
  display: block;
  width: 100%;
  margin: 2.5em 0 3.5em;
  padding: 0;
  height: 1px;
  border: 0;
  border-top: 1px solid color(var(--lightgrey) l(+10%));
}

audio,
canvas,
iframe,
img,
svg,
video {
  vertical-align: middle;
}

fieldset {
  margin: 0;
  padding: 0;
  border: 0;
}

textarea {
  resize: vertical;
}

p,
ul,
ol,
dl,
blockquote {
  margin: 0 0 1.5em 0;
}

ol,
ul {
  padding-left: 1.3em;
  padding-right: 1.5em;
}

ol ol,
ul ul,
ul ol,
ol ul {
  margin: 0.5em 0 1em;
}

ul {
  list-style: disc;
}

ol {
  list-style: decimal;
}

ul,
ol {
  max-width: 100%;
}

li {
  margin: 0.5em 0;
  padding-left: 0.3em;
  line-height: 1.6em;
}

dt {
  float: left;
  margin: 0 20px 0 0;
  width: 120px;
  color: var(--darkgrey);
  font-weight: 500;
  text-align: right;
}

dd {
  margin: 0 0 5px 0;
  text-align: left;
}

blockquote {
  margin: 1.5em 0;
  padding: 0 1.6em 0 1.6em;
  border-left: var(--whitegrey) 0.5em solid;
}

blockquote p {
  margin: 0.8em 0;
  font-size: 1.2em;
  font-weight: 300;
}

blockquote small {
  display: inline-block;
  margin: 0.8em 0 0.8em 1.5em;
  font-size: 0.9em;
  opacity: 0.8;
}
/* Quotation marks */
blockquote small:before {
  content: "\2014 \00A0";
}

blockquote cite {
  font-weight: bold;
}
blockquote cite a {
  font-weight: normal;
}

a {
  color: color(var(--blue) l(-5%));
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  line-height: 1.15;
  font-weight: 600;
  text-rendering: optimizeLegibility;
}

h1 {
  font-size: 52px;
  color: #FFFFFF;
  line-height: 64px;
  font-weight: 500;
  letter-spacing: 0.5px;
  padding-left: 55px;
  margin-bottom: 48px;

  &.dark {
    color: #030303;
  }

  &.text-center {
    padding-left: 0px;
  }

  @media screen and (max-width: 1024px) {
    font-size: 40px;
    line-height: 45px;
  }

  @media screen and (max-width: 768px) {
    padding-left: 0;
    font-size: 20px;
    line-height: 25px;
    margin-bottom: 20px;
  }
}


h2 {
  font-weight: 700;
  font-size: 40px;
  color: #030303;
  line-height: 52px;
  margin: 0;
  padding: 0;
  margin-bottom: 0.5em;

  &:after {
    content: '';
    width: 80px;
    height: 2px;
    background: #1E9AF7;
    display: block;
    margin-top: 25px;
    margin-bottom: 32px;
  }

  &.text-center {
    &:after {
      content: '';
      margin: 25px auto 0;
    }
  }

  &.white {
    color: white;
  }

  &.no-line {
    margin-bottom: 60px;

    &:after {
      content: none;
    }

    &.no-padding {
      margin-bottom: 0px;
    }

    @media screen and (max-width: 768px) {
      margin-bottom: 30px;
    }
  }

  &.margin-bottom-sm {
    margin-bottom: 25px;

    @media screen and (max-width: 768px) {
      margin-bottom: 10px;
    }
  }

  @media screen and (max-width: 768px) {
    font-size: 20px;
    line-height: 25px;

    &:after {
      margin-top: 13px;
      margin-bottom: 8px;
    }
  }

  @media (max-width: 500px) {
    font-size: 1.8rem;
  }
}

h3 {
  font-size: 32px;
  color: #030303;
  line-height: 40px;
  display: block;
  font-weight: normal;
  margin-bottom: 25px;

  @media screen and (max-width: 768px){
    font-size: 20px;
    line-height: 26px;
    padding-top: 15px;
    margin-bottom: 15px;
  }
}

h4 {
  font-weight: 600;
  font-size: 17px;
  color: #030303;
  line-height: 28px;
  display: block;

  &.normal {
    font-weight: normal;
  }

  @media screen and (max-width: 768px) {
    font-size: 14px;
    line-height: 18px;
  }
}

h5 {
  margin: 1.5em 0 0.5em 0;
  font-size: 1.4rem;
  font-weight: 500;
}

h6 {
  margin: 1.5em 0 0.5em 0;
  font-size: 1.4rem;
  font-weight: 500;
}

.row {
  margin-left: 55px;
  margin-right: 55px;

  padding-top: 80px;
  padding-bottom: 60px;

  &.no-padding {
    margin-left: -15px;
    margin-right: -15px;

    padding-top: 0px;
    padding-bottom: 0px;
  }
}

p, li.p {
  font-size: 20px;
  color: #030303;
  line-height: 35px;
  margin-bottom: 20px;

  @media screen and (max-width: 768px) {
    font-size: 14px;
    line-height: 18px;
  }
}

@media (max-width: 768px) {
  .row {
    margin-left: -15px;
    margin-right: -15px;

    padding-top: 40px;
    padding-bottom: 40px;

    &.no-padding {
      margin-left: 0px;
      margin-right: 0px;

      padding-top: 0px;
      padding-bottom: 0px;
    }
  }
}

.hero {
  min-height: 600px;
  position: relative;
  overflow: hidden;

  h1 {
    &.text-center, &.no-image {
      max-width: 100%;
    }

    &.partial-width {
      max-width: 50%;
    }
  }

  a {
    display: inline-block;
    white-space: nowrap;
  }

  .bg-img {
    img {
      max-height: 600px;
    }
  }

  @media screen and (max-width:1550px) {
    min-height: 450px;
  }

  @media screen and (max-width:768px) {
    min-height: 350px;

    h1 {
      max-width: 100%;

      &.partial-width {
        max-width: 100%;
      }
    }

    .bg-img {
      position: absolute;
      top: 0px;
      right: 0px;

      > img {
        min-height: 250px;
      }
    }
  }

  &.hero-no-image {
    min-height: 400px;
    height: 400px;

    h1 {
      max-width: 100%;
    }

    p {
      margin-bottom: 0px;
    }

    .hero-content {
      padding-bottom: 0px;
    }

    @media screen and (max-width:768px) {
      min-height: 250px;
      height: 250px;
    }
  }

  &.hero-title-only {
    min-height: 300px;
    height: 300px;

    h1 {
      max-width: 100%;
      margin-bottom: 10px;
    }

    .hero-content {
      padding-bottom: 0px;
    }

    @media screen and (max-width:768px) {
      min-height: 200px;
      height: 200px;
    }
  }
}

.xy-center {
  &:before {
    content: ' ';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
  }

  > * {
    display: inline-block;
    position: relative;
    vertical-align: middle;
  }
}

.hero-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;

  @media screen and (max-width: 768px) {
    padding-bottom: 34px;
    align-items: flex-end;

    top: auto;
    bottom: 30px;
    padding-bottom: 0px;
  }

  .secondary-btn-large {
    margin-left: 55px;
    color: #fff;
    margin-right: 18px;

    @media screen and (max-width: 768px) {
      margin-left: 0;
      margin-right: 5px;
    }

    &.blue-text {
      color: #1E9AF7;
    }
  }

  .text-center .secondary-btn-large {
    margin-left: 18px;
  }

  p {
    color: white;
  }
}

.clear {
  clear: both;
}

.item-link {
  cursor: pointer;
}

.legal-content {
  p, li {
    font-size: 17px;
    line-height: 26px;
    margin-bottom: 15px;
  }

  h3 {
    margin-top: 50px;
    margin-bottom: 25px;
    font-weight: 600;
  }
}
