.faq-content {
  position: relative;
}

.page-faq .cta-banner {
  border-top: 1px solid #DAE6F0;
}

.faq-questions hr {
  margin-top: 20px;
  margin-bottom: 20px;
}

.faq-questions h2 {
  font-weight: 500;
}

.faq-questions h3, .faq-questions h4 {
  font-size: 22px;
  font-weight: 500;

  @media (max-width: 768px) {
    font-size: 14px;
    font-weight: 500;
    line-height: 20px;
    margin-bottom: 0px;
    padding-top: 10px;
  }
}

.faq-questions .hidden-xs {
  @media (max-width: 768px) {
    display: none;
  }
}

.faq-questions p {
  margin-bottom: 48px;

  @media (max-width: 768px) {
    margin-top: 8px;
    margin-bottom: 15px;
  }
}

@media (max-width: 980px) {
  .faq-questions h3 {
    cursor: pointer;
  }

  .faq-questions h3:before {
    color: #7EC8FF;
    content: '+';
    width: 48px;
    height: 48px;
    margin-right: 12px;
  }

  .faq-questions h3.open:before {
    content: '';
  }

  .faq-questions p {
    display: none;
  }

  .faq-questions p.open {
    display: block;
  }
}
