.hero-screen-features {
  position: relative;
  background: #133247;

  .hero-content {
    .secondary-btn-large {
      cursor: pointer;
      padding-left: 40px;

      @media screen and (max-width: 768px){
        padding-left: 20px;
      }

      .fa {
        margin-right: 10px;
        width: 15px;
        display: inline-block;

        @media screen and (max-width: 768px){
          margin-right: 2px;
        }
      }
    }
  }

  .bg-img {
    text-align: right;

    img {
      max-height: 580px;
      margin-top: 41px;

      @media screen and (max-width: 1635px){
        width: 56%;
      }

      @media screen and (max-width: 768px){
        width: 85%;
        min-height: 0px;
        margin-top: 15px;
      }
    }
  }
}

.features-h2 {
  font-size: 20px;
  line-height: 30px;
  margin-bottom: 55px;
  opacity: .5;
  padding-left: 30%;
  padding-right: 30%;

  @media screen and (max-width: 768px){
    font-size: 15px;
    line-height: 20px;
    margin-bottom: 30px;
    padding-left: 5%;
    padding-right: 5%;
  }
}

.features-bundles-container {
  margin-left: 25%;
  margin-right: 25%;
  padding-bottom: 30px;

  @media screen and (max-width: 768px){
    margin-left: 5%;
    margin-right: 5%;
    padding-bottom: 12px;
  }
}

.features-hardware {
  .banner-img {
    background: #FFFFFF;
    box-shadow: 0 3px 40px 0 rgba(0,0,0,0.05);
    border-radius: 4.8px;
    margin-top: 60px;
    overflow: hidden;

    img {
      max-height: 607px;
      display: block;
      margin: 0 auto;
    }
  }

  .col-sm-12 .col-sm-12 {
    padding-left: 0px;
    padding-right: 0px;
  }

  @media screen and (max-width: 768px) {
    .banner-img {
      margin-top: 30px;
    }

    .row > div {
      margin-top: 30px;

      &:first-of-type {
        margin-top: 0px;
      }
    }
  }
}

.features-sliders {
  margin-top: 60px;

  .features-slider {
    border-radius: 4.8px;

    .owl-stage-outer {
      background: #FFFFFF;
      box-shadow: 0 3px 40px 0 rgba(0,0,0,0.05);
    }

    .owl-item {
      img {
        width: auto;
        margin: 31px auto;
        display: block;
        max-height: 657px;
      }
    }

    .owl-dots {
      text-align: center;
      margin-top: 40px;

      @media screen and (max-width: 768px) {
        display: none;
      }

      .owl-dot {
        width: 16px;
        height: 16px;
        border: 2px solid #1E9AF7;
        border-radius: 50%;
        margin: 0 10px;

        &.active {
          background: #1E9AF7;
        }
      }
    }
  }
}

.b-care-apps {
  padding-top: 80px;
  padding-bottom: 80px;

  @media screen and (max-width: 768px) {
    padding: 40px 0;
  }

  .b-title {
     margin-bottom: 40px;
  }

  .col-md-4 {
    margin-bottom: 30px;
  }

  .item {
    background: #FFFFFF;
    box-shadow: 0 6px 13px 0 rgba(50,50,93,0.14);
    border-radius: 6.4px;
    padding: 0 60px;
    height: 100%;
    width: 100%;
    justify-content: center;

    @media screen and (max-width: 1280px) {
      padding: 0 15px;
    }

    &.item-bg-blue {
      background: #FFFFFF;
    }

    .text {
      padding-bottom: 30px;
      width: 100%;
    }

    .title {
      font-size: 32px;
      color: #030303;
      text-align: center;
      line-height: 52px;
      padding-top: 40px;
      width: 100%;

      @media screen and (max-width: 768px) {
        font-size: 20px;
        padding-top: 20px;
      }
    }
    
    .features-title {
      font-size: 24px;
      color: #030303;
      text-align: center;
      line-height: 36px;
      padding-top: 30px;
      padding-bottom: 10px;

      @media screen and (max-width: 768px) {
        font-size: 20px;
        padding-top: 20px;
      }
    }

    h4 {
      font-weight: normal;
      opacity: .5;
    }

    ul {
      margin-top: 15px;
      padding: 0;
      list-style-type: none;

      @media screen and (max-width: 1024px) {
        padding-left: 40px;
        padding-right: 40px;
      }
    }

    li {
      font-size: 15px;
      line-height: 25px;
      color: rgba(0, 0, 0, .7);
      padding-top: 0px;
      margin-bottom: 0;
      text-align: left;
      width: 100%;
    }

    .img-bottom {
      display: block;
      margin: auto auto 0;
      max-height: 220px;
    }

    .item-img-container {
      padding-top: 50px;
      display: flex;
      justify-content: center;
    }

    .img-top {
      display: block;
      height: 90px;
      object-fit: cover;
    }
  }
}

.features-list {
  ul {
    list-style-type: circle;
    padding-left: 30px;

    @media screen and (max-width: 768px){
      margin-bottom: 0;
    }

    li {
      font-size: 20px;
      color: #030303;
      line-height: 35px;
      margin: 0;

      @media screen and (max-width: 768px) {
        font-size: 16px;
        line-height: 24px;
      }
    }
  }

  .row:not(:first-of-type) {
    padding-top: 20px;
  }

  .image {
    background-size: cover;
    background-position: center;
    display: block;
    width: 98%;
    height: 350px;
  }
}

/* video stuff */
#landing-popup-video {
  background: rgba(0, 0, 0, 0.65);
  position: fixed;
  top: 0;
  left: 100%;
  height: 100%;
  opacity: 0;
  overflow-y: auto;
  pointer-events: none;
  text-align:center;
  transition: opacity 0.4s;
  visibility: hidden;
  width: 100%;
  z-index: 20;
}

#landing-popup-video-close {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 21;

  > img {
    width: 20px;
    height: 20px;
  }
}

#landing-popup-video:before {
  content: ' ';
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}

#landing-popup-video > iframe {
  display: inline-block;
  margin: 0px;
  vertical-align: middle;
}

#landing-popup-video.active {
  opacity: 1;
  pointer-events: auto;
  left: 0;
}
