.hero-screen-sellers {
  overflow: hidden;

  .bg-img {
    text-align: right;
    min-height: 510px;
    margin-top: 40px;

    @media screen and (max-width:4050px) {
      min-height: 450px;
    }

    @media screen and (max-width:768px) {
      min-height: 300px;
    }

    img {
      max-width: 100%;
      min-height: 500px;
    }
  }

  @media screen and (max-width:4050px) {
    .hero-content {
      margin-top: 30px;
    }

    .bg-img {
      margin-top: 55px;

      img {
        width: 64%;
      }
    }
  }

  @media screen and (max-width:768px) {
    .hero-content {
      margin-top: 60px;
    }

    .bg-img {
      margin-top: 0px;

      img {
        min-height: 0px;
        width: 100%;
      }
    }
  }
}

.checklists {
  display: flex;
  flex-direction: row;

  @media screen and (max-width:768px) {
    flex-direction: column;
  }

  .checklist {
    ul {
      margin-top: 0px;
    }
  }
  .first-list {
    padding-right: 30px;

    @media screen and (max-width:1024px) {
      padding-right: 15px;
    }
    @media screen and (max-width:768px) {
      padding-right: 0px;
    }
  }
}

.new-section {
  margin-bottom: 30px;

  .b-title {
    font-size: 30px;
    color: #030303;
    text-align: center;
    line-height: 40px;
    font-weight: bold;

    @media screen and (max-width: 768px) {
      font-size: 18px;
      line-height: 20px;
    }
  }
}

.offerings {
  display: flex;
  flex-direction: row;

  @media screen and (max-width: 768px) {
    flex-direction: column;
  }

  .offering {
    flex: 1;
    padding-left: 10px;
    padding-right: 10px;

    @media screen and (max-width: 768px) {
      padding-left: 55px;
      padding-right: 55px;
      margin-bottom: 20px;
    }

    .o-img {
      margin-bottom: 20px;
      max-width: 110px;
      max-height: 110px;

      @media screen and (max-width: 768px) {
        margin-bottom: 12px;
        max-width: 90px;
        max-height: 90px;
      }
    }

    .resellers-o-img {
      margin-top: 15px;
      margin-bottom: 40px;
      max-height: 150px;

      @media screen and (max-width: 768px) {
        margin-bottom: 12px;
        max-width: 90px;
        max-height: 90px;
      }
    }

    .o-header {
      font-weight: 800;
      font-size: 22px;
      padding-bottom: 10px;

      @media screen and (max-width: 768px) {
        padding-bottom: 5px;
        font-size: 18px;
      }
    }

    .o-text {
      margin-top: 6px;
      opacity: .75;
      font-size: 15px;

      @media screen and (max-width: 768px) {
        margin-top: 0px;
        font-size: 14px;
      }
    }
  }
  .last-offering {
    @media screen and (max-width: 768px) {
      margin-bottom: 0px;
    }
  }

  .reseller-o {
    padding-left: 25px;
    padding-right: 25px;
  }
}

.ways-sell-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 40px;
  
  @media screen and (max-width: 768px) {
    overflow-x: scroll;
    overflow-y: hidden;
  }

  .way-sell {
    display: flex;
    flex-direction: column;
    flex-basis: 0;
    flex-grow: 1;
    background-color: white;
    margin-left: 15px;
    margin-right: 15px;
    padding: 40px 20px 20px 20px;
    border-radius: 10px;
    align-items: center;
    -webkit-box-shadow: 0px 5px 12px -8px rgba(122,122,122,1);
    -moz-box-shadow: 0px 5px 12px -8px rgba(122,122,122,1);
    box-shadow: 0px 5px 12px -8px rgba(122,122,122,1);
    max-width: 400px;

    @media screen and (max-width: 768px) {
      margin-left: 10px;
      margin-right: 10px;
    }

    .ws-img {
      margin-bottom: 30px;
      max-width: 130px;
      max-height: 130px;
      object-fit: contain;
      padding: 0px;

      @media screen and (max-width: 768px) {
        margin-bottom: 20px;
        max-width: 80px;
        max-height: 80px;
        width: 240px;
      }
    }

    .ws-bold-blue {
      font-weight: 600;
      color: #1E9AF7;
    }
    .ws-bold {
      font-weight: 600;
    }

    .ws-text {
      width: 80%;

      @media screen and (max-width: 768px) {
        width: 240px;
      }

      .ws-title {
        font-weight: 600;
        font-size: 20px;

        @media screen and (max-width: 768px) {
          font-size: 18px;
        }
      }
  
      .ws-tagline {
        margin-top: 5px;
        opacity: .5;
        font-size: 14px;

        @media screen and (max-width: 768px) {
          margin-top: 10px;
          font-size: 13px;
          line-height: 16px;
        }
      }
  
      .ws-body {
        margin-top: 20px;
        text-align: left;

        @media screen and (max-width: 768px) {
          margin-top: 15px;
          font-size: 14px;
        }
  
        ul {
          text-align: left;
        }
      }
    }
  }
}