.b-press {
  .col-md-4 {
    margin-top: 40px;

    @media screen and (max-width: 768px){
      margin-top: 15px;
    }
  }

  .item {
    box-shadow: 0 4px 11px 0 rgba(0,0,0,0.20);
    border-radius: 6px;
    padding: 20px 20px 40px;
    height: 100%;

    @media screen and (max-width: 768px){
      height: auto;
      padding: 15px 20px;
    }

    a {
      display: block;
    }

    a:hover {
      text-decoration: none;
    }

    span {
      font-size: 17px;
      color: #627793;
      line-height: 28px;

      @media screen and (max-width: 768px){
        font-size: 14px;
        line-height: 20px;
      }
    }

    p {
      font-size: 20px;
      color: #030303;
      line-height: 35px;
      font-weight: bold;
      padding-top: 20px;

      @media screen and (max-width: 768px){
        font-size: 16px;
        line-height: 20px;
      }
    }
  }
}

.b-news {
  .news-list {
    background: #375270;

    ul {
      max-width: 800px;
      margin: 0 auto;
      list-style: none;
      padding: 0;

      li {
        display: flex;
        padding: 24px 0;
        border-bottom: 1px solid #a6baca;
      }

      .img {
        margin-right: 25px;
        width: 96px;

        img {
          max-width: 100%;
        }
      }

      .text {
        span {
          color: #a6baca;
          display: block;
        }

        a {
          color: #fff;
          display: block;
          font-weight: 500;
          line-height: 1.5;
          margin-top: 12px;
        }
      }
    }
  }
}

.b-video-gallery {
  .owl-item {
    padding: 15px 0;
  }

  .item {
    text-align: center;
    box-shadow: 0 4px 11px 0 rgba(0,0,0,0.20);
    border-radius: 6px;
    padding-bottom: 25px;

    a {
      &:hover {
        text-decoration: none;
      }
    }

    .img {
      border-radius: 6px 6px 0 0;
      min-height: 234px;
      background-position: center;
      background-size: cover;
    }

    span {
      color: #a6baca;
      display: block;
      font-size: 12px;
      margin: 12px 0;
    }

    h5 {
      padding: 0;
      margin: 0;
      min-height: 35px;
    }
  }

  .owl-dots {
    text-align: center;
    margin-top: 40px;

    @media screen and (max-width: 768px) {
      display: none;
    }

    .owl-dot {
      width: 16px;
      height: 16px;
      border: 2px solid #1E9AF7;
      border-radius: 50%;
      margin: 0 10px;

      &.active {
        background: #1E9AF7;
      }
    }
  }
}

.b-gallery {
  padding-top: 80px;
  .owl-item {
    padding: 15px 0;
  }

  .item {
    text-align: center;
    box-shadow: 0 4px 11px 0 rgba(0, 0, 0, 0.20);
    border-radius: 6px;
    overflow: hidden;
  }

  .owl-dots {
    text-align: center;
    margin-top: 40px;

    @media screen and (max-width: 768px) {
      display: none;
    }

    .owl-dot {
      width: 16px;
      height: 16px;
      border: 2px solid #1E9AF7;
      border-radius: 50%;
      margin: 0 10px;

      &.active {
        background: #1E9AF7;
      }
    }
  }
}

.b-resources {
  padding: 170px 0;

  @media screen and (max-width: 768px){
    padding: 40px 0;
  }

  p {
    padding-top: 55px;
  }
}
