.search-container {
  background: rgba(55, 82, 112, 0.9);
  display: none;
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
}

.search-container.open {
  text-align: center;
  -js-display: flex;
  display: flex;
  justify-content: center;
  align-items: center;
}

.search-body {
  border-radius: 8px;
  opacity: 0;
  max-width: 1200px;
  padding: 48px;
  position: relative;
  transition: 0.6s;
  width: 75%;
}

.search-body > form > div {
  position: relative;
  width: 100%;
  height: 100%;
  white-space: nowrap;
}

.open > .search-body {
  transform: translateZ(0);
  -webkit-animation: fade-in-down 0.4s both;
  animation: fade-in-down 0.4s both;
  -webkit-animation-delay: 0.1s;
  animation-delay: 0.1s;
}

.search-results {
  position: relative;
}

.search-default {
  background-color: white;
  border-radius: 8px;
  -js-display: flex;
  display: flex;
  margin-top: 48px;
  padding: 48px;
  text-align: left;
}

.search-default.hidden {
  -webkit-animation: fade-out-down 0.2s both;
  animation: fade-out-down 0.2s both;
}

#search-results {
  max-height: 280px;
  overflow: hidden;
}

.search-results-column {
  flex: 1 1 50%;
  min-width: 40%;
}

.search-results-zendesk {
  overflow: hidden;
}

.search-results-column:not(:last-of-type) {
  flex: 1 1 50%;
  margin-right: 48px;
}

.search-results-column > * {
  text-align: left;
}

.search-results-column h3 {
  margin-bottom: 12px;
}

.search-results-column ul {
  padding: 0px;
}

.search-response, .search-results-column li {
  line-height: 1.75;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  font-size: 14px;
  line-height: 18px;
  padding-left: 0px;
  margin-top: 15px;
  margin-left: 0px;
}

#search-close {
  color: white;
  cursor: pointer;
  font-size: 30px;
  opacity: 0.6;
  padding: 24px;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 10;
  -webkit-animation: fade-in 0.4s both;
  animation: fade-in 0.4s both;
}

#search-close:hover {
  opacity: 1;
}

#search-input {
  color: white;
  font-family: objektiv-mk2, sans-serif, 'Avenir Next W02','Avenir Next','Proxima Nova','Myriad Pro',Helvetica,Calibri,sans-serif;
  font-size: 36px;
  outline: none;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid white;
  padding-left: 48px;
  width: 100%;
}

.search-bar > .fa {
  color: white;
  font-size: 30px;
  position: absolute;
  left: 0;
  bottom: 14px;
}

.search-response {
  background-color: white;
  border-radius: 8px;
  -js-display: flex;
  display: flex;
  max-height: 420px;
  opacity: 0;
  overflow-y: auto;
  padding: 48px;
  position: absolute;
  text-align: left;
  top: 0;
  transition: 0.2s;
  transform: translateY(-12px);
  width: 100%;
  z-index: -1;
}

.search-response.active {
  opacity: 1;
  transform: translateY(0);
  z-index: 1;
}

.search-response h3 {
  margin-bottom: 12px;
}

.search-help-loading {
  text-align: center;
}

.search-help-loading > .fa {
  color: #A6BACA;
  font-size: 42px;
  margin-top: 24px;
}

@media (max-width: 980px) {
  .search-body {
    padding: 12px;
    top: 0;
    width: 95%;
  }

  .search-response, .search-default {
    flex-direction: column;
    padding: 24px;
  }

  .search-results-column {
    margin-bottom: 24px;
  }
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fade-out-down {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(100px);
    transform: translateY(100px);
  }
}
@keyframes fade-out-down {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(100px);
    transform: translateY(100px);
  }
}
