.hero-screen-developers {
  position: relative;
  background: #133247;

  .bg-img {
    &:after {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background-image: linear-gradient(90deg, #133247 16%, rgba(19,50,71,0.00) 100%);
    }

    @media screen and (max-width:768px) {
      > img {
        width: 180%;
        max-height: 350px;
        max-width: none;
        float: right;
      }
    }
  }
}

.developer-section-1 {
  img {
    max-height: 550px;
    margin: -30px 0px -20px;
  }
}

.developers-section-2 {
  img {
    max-height: 683px;
    text-align: right;
  }
}

.developers-features {
  h2 {
    margin-bottom: 30px;

    @media screen and (max-width: 768px) {
      margin-bottom: 20px;
    }
  }

  .secondary-btn-row {
    margin-bottom: 50px;

    @media screen and (max-width: 768px) {
      margin-bottom: 15px;
    }
  }

  .developers-features-list {
    text-align: center;

    > div {
      display: inline-block;
      margin: 20px;
      vertical-align: center;
      text-align: center;

      @media screen and (max-width: 768px) {
        margin: 10px;
      }

      > p {
        color: #616162;
        font-weight: 500;
      }

      > .box {
        background: #FFFFFF;
        box-shadow: 0 10px 20px 0 rgba(0,0,0,0.10);
        border-radius: 16px;
        margin-bottom: 20px;
        display: block;
        cursor: pointer;
        text-decoration: none;
        width: 200px;
        height: 200px;
        padding: 20px;
        padding-top: 45px;
        position: relative;
        box-sizing: border-box;
        transition: box-shadow 0.2s;

        @media screen and (max-width: 768px) {
          zoom: 0.8;
        }

        &:hover {
          box-shadow: 0 10px 20px 0 rgba(0,0,0,0.15);
        }

        > .icon {
          > img {
            width: 70px;
          }
        }

        > .logo {
          position: absolute;
          bottom: 0px;
          height: 65px;
          left: 0px;
          width: 100%;
          padding: 15px;
          box-sizing: border-box;

          > img {
            max-width: 130px;
            max-height: 30px;
          }
        }
      }
    }
  }
}

.developers-faq {
  @media screen and (max-width: 768px){
    padding: 32px 0;
  }

  .sub-title {
    margin-bottom: 30px;
  }
}

.developers-faq .secondary-btn-small, .developers-features .secondary-btn-small {
  color: #1E9AF7;
  padding: 11px 50px 12px;
  margin-top: 20px;
  margin-left: 10px;
  margin-right: 10px;
  display: inline-block;
}

.developers-faq {
  .b-title {
    @media screen and (max-width: 768px) {
      h2 {
        font-size: 20px;
        line-height: 25px;
        padding-bottom: 10px;
      }
    }
  }

  .list {
    max-width: 1280px;
    margin: 40px auto 0;

    .item {
      background: #FFFFFF;
      border: 1px solid #E3EAEF;
      box-shadow: 0 10px 20px 0 rgba(50,50,93,0.10);
      border-radius: 8px;
      margin-bottom: 10px;
      min-width: 800px;

      @media screen and (max-width: 850px) {
        width: 100%;
        min-width: 0px;
      }

      .item-link {
        display: flex;
        justify-content: space-between;
        padding: 30px 0 25px;
        margin: 0 40px;


        &.active {
          border-bottom: 1px solid #E3EAEF;

          .fa {
            transform: rotate(180deg);
            margin-bottom: 8px;
          }
        }

        &:hover {
          text-decoration: none;
        }

        .fa {
          color: #1E9AF7;
          margin-top: 8px;
          transition: all 0.5s;
          height: 16px;
        }

        span {
          font-size: 20px;
          color: #030303;
          line-height: 35px;
          font-weight: bold;
          display: inline-block;
          margin-right: 35px;
          width: 160px;

          &.long {
            margin-right: 65px;
            width: auto;
          }

          @media screen and (max-width: 768px) {
            font-size: 16px;
          }
        }

        p {
          font-size: 20px;
          color: #030303;
          line-height: 35px;
          display: inline-block;
          margin: 0;
          padding-right: 30px;

          @media screen and (max-width: 768px) {
            font-size: 14px;
            line-height: 18px;
          }
        }
      }

      .item-content {
        padding: 20px 40px;
        display: none;

        .learn-more-link {
          display: block;
          margin-bottom: 20px;

          @media screen and (max-width: 768px) {
            font-size: 14px;

            &:after {
              margin-top: 7px;
            }
          }
        }

        ul {
          list-style: none;
          padding: 0;
          margin: 0;

          li {
            span {
              font-size: 14px;
              color: #627793;
              line-height: 35px;
            }

            .learn-more-link {
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }
}
