.hero-screen-post {
  height: 400px;
  background: #133247;
  background-position: center;
  background-size: cover;
  position: relative;
  text-align: center;


  .hero-mask {
    position: absolute;
    background: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100%;
    top: 0px;
    left: 0px;
    z-index: 1;
  }

  .hero-content {
    position: relative;
    z-index: 2;
  }
}

.blog-post-container {
  .back {
    color: #0d9ded;
    font-weight: 500;
    display: block;
    margin-bottom: 40px;

    @media screen and (max-width: 768px) {
      margin-bottom: 25px;
      font-size: 14px;
      line-height: 22px;
    }
  }

  .blog-post-content {
    margin-bottom: 50px;

    p, li {
      font-size: 17px;
      line-height: 26px;
      margin-bottom: 15px;
    }

    h1, h2, h3 {
      font-size: 32px;
      color: #030303;
      line-height: 40px;
      display: block;
      margin-top: 50px;
      margin-bottom: 25px;
      font-weight: 500;
    }

    @media screen and (max-width: 768px) {
      margin-bottom: 30px;

      p, li {
        font-size: 14px;
        line-height: 22px;
        margin-bottom: 10px;
      }

      h1, h2, h3 {
        font-size: 17px;
        line-height: 26px;
        margin-bottom: 15px;
        margin-top: 15px;
      }
    }
  }
}
