.b-sales-section-1 {
  text-align: center;

   .img {
     border-radius: 50%;
     height: 200px;
     width: 200px;
     display: block;
     margin: 40px auto 0;
   }

   h3 {
      margin-top: 30px;
      font-size: 22px;
      line-height: 30px;
      font-weight: 600;
   }

   p {
     font-size: 17px;
     line-height: 24px;
   }
 }

.sales-hub-section {
  h2.text-center {
    margin-bottom: 20px;
  }

  p.text-center {
    margin-bottom: 50px;
  }
}

.sales-hub-media {
  -js-display: flex;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.sales-hub-media-container {
  margin-bottom: 35px;
}

.sales-hub-media-item {
  display: inline-block;
  width: 290px;
  position: relative;
  text-align: center;
}

.sales-hub-media-item:hover .sales-hub-media-hover {
  opacity: .95;
}

.sales-hub-media-hover {
  background-color: white;
  color: #30373b;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: 0.1s;
}

.bg-light-blue .sales-hub-media-hover {
  background: #EAF0F3;
}

.sales-hub-media-hover > div {
  padding: 48px 24px;
}

.sales-hub-media-hover h4 {
  margin-bottom: 12px;
}

.sales-hub-media-hover p {
  font-size: 14px;
  line-height: 28px;
}

.sales-hub-download {
  background-image: url('https://d85ecz8votkqa.cloudfront.net/images/poyntdotcom/sales/icon-dl.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  height: 72px;
  width: 72px;
  margin: 24px auto 0 auto;
}

.sales-hub-media-preview {
  text-align: center;
}

.sales-hub-media-preview p {
  color: #375270;
}

.sales-hub-image-thumbnail {
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  height: 250px;
  margin: 24px auto;
  width: 290px;
}
