.b-join {
  padding: 80px 0;

  @media screen and (max-width: 768px) {
    padding: 40px 0;

    img {
      margin-top: 20px;
    }
  }

  h2 {
    font-weight: 700;
    font-size: 40px;
    color: #030303;
    line-height: 52px;
    margin: 0;
    padding: 0;

    &:after {
      content: '';
      width: 80px;
      height: 2px;
      background: #1E9AF7;
      display: block;
      margin-top: 25px;
      margin-bottom: 32px;
    }

    @media screen and (max-width: 768px) {
      font-size: 20px;
      line-height: 25px;
      padding-top: 32px;

      &:after {
        margin-top: 13px;
        margin-bottom: 8px;
      }
    }
  }

  p {
    font-size: 20px;
    color: #030303;
    line-height: 35px;
    margin-bottom: 20px;
    padding-bottom: 20px;

    @media screen and (max-width: 768px) {
      font-size: 14px;
      line-height: 18px;
    }
  }
}

.b-team {
  .img {
    border-radius: 50%;
    height: 200px;
    width: 200px;
    background: #D8D8D8;
    display: block;
    margin: 40px auto 0;
  }

  span {
    font-size: 17px;
    color: #030303;
    text-align: center;
    line-height: 28px;
    font-weight: bold;
    display: block;
    padding-top: 20px;
  }

  h4 {

  }
}

.b-rewarding {
  .b-rewarding-slider {
    padding-top: 40px;

    img {
      height: 375px;
    }

    @media screen and (max-width: 768px){
      padding-top: 0;
    }

    img {
      width: 100%;
    }
  }
}

.b-rewarding-info {
  span {
    font-size: 20px;
    color: #030303;
    line-height: 35px;
    font-weight: bold;
    padding: 40px 0;
    display: block;

    @media screen and (max-width: 768px){
      padding: 15px 0;
    }
  }

  p {
    font-size: 17px;
    color: #030303;
    line-height: 28px;
    margin: 0;
  }
}
