/*** text formatting ***/
.sm-blue-underline {
  &:after {
    content: '';
    width: 80px;
    height: 2px;
    background: $primary-site-color;
    display: block;
    margin-top: 32px;
    margin-bottom: 32px;
    margin-left: auto;
    margin-right: auto;
  }
}
.b-title {
  font-size: 40px;
  color: #030303;
  text-align: center;
  line-height: 52px;
  font-weight: bold;

  @media screen and (max-width: 768px) {
    font-size: 20px;
    line-height: 25px;
  }
}

.text-center {
  text-align: center;
}
.text-left {
  text-align: left;
}
.text-right {
  text-align: right;
}
.text-white {
  color: white;
}
.text-hero {
  font-size: 60px;
  line-height: 64px;
  font-weight: 500;
  letter-spacing: 0.5px;
}
.p-center-container {
  width: 75%;
  margin-left: auto;
  margin-right: auto;

  .p-center {
    text-align: center;
  }
}

.p-small-margin {
  margin-top: 10px;
  margin-bottom: 5px;
}
.div-no-margin {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 14px;
}

.mobile-only {
  display: none;

  @media screen and (max-width: 768px) {
    display: block;
  }
}
.mobile-push-down {
  padding-top: 50px;

  @media screen and (max-width: 768px) {
    padding-top: 150px;
  }
  @media screen and (max-width: 400px) {
    padding-top: 120px;
  }
}

/*** layout ***/
.mb-s {
  margin-bottom: 40px;
}
.mb-m {
  margin-bottom: 80px;
}
.mb-l {
  margin-bottom: 120px;
}
.mt-s {
  margin-top: 40px;
}
.mt-m {
  margin-top: 80px;
}
.mt-l {
  margin-top: 120px;
}
.bottom-padding {
  padding-bottom: 0px;
}
.no-top-padding {
  padding-top: 0px;
  padding-bottom: 60px;
}
.img-padding-sm-r {
  padding-right: 40px;
}
.img-padding-r {
  padding-right: 45px;

  @media screen and (max-width: 768px) {
    padding-right: 0px;
  }
}
.img-padding-lg-r {
  padding-right: 70px;

  @media screen and (max-width: 768px) {
    padding-right: 0px;
  }
}
.img-padding-l {
  padding-left: 45px;

  @media screen and (max-width: 768px) {
