}

.container {
  max-width: 1680px;
}

.bg-dark-blue {
  background: $fifth-neutral-color;
  color: white;
}
.bg-grey {
  background: #EAF0F3;
}
.bg-white {
  background: #fff;
}
.bg-light-blue {
  background: #EAF0F3;
}
.learn-more-link {
  font-size: 18px;
  color: #000000;
  font-weight: 500;

  &:after {
    content: '';
    width: 20px;
    height: 16px;
    display: inline-block;
    vertical-align: middle;
    margin: -3px 0 0 10px;
    background: url('https://d85ecz8votkqa.cloudfront.net/images/booynt/icons/arrow_ic.svg') no-repeat;
  }

  &:hover {
    text-decoration: none;
  }

  &.no-arrow {
    color: #999;

    &:after {
      content: none;
    }
  }

  @media screen and (max-width: 768px){
    font-size: 14px;
    margin-bottom: 20px;
    display: block;

    &:after {
      width: 15px;
      height: 11px;
      background-size: cover;
    }
  }
}

.learn-more-row {
  margin-top: 25px;

  .learn-more-link {
    margin-bottom: 0px;

    @media screen and (max-width: 768px){
      margin-bottom: 0px;
    }
  }

  & + .learn-more-row {
    margin-top: 15px;

    @media screen and (max-width: 768px){
      margin-top: 5px;
    }
  }
}
/*** buttons ***/
.primary-btn-large {
  background: #1E9AF7;
  border-radius: 25px;
  font-weight: bold;
  font-size: 18px;
  color: #FFFFFF;
  padding: 11px 50px 12px 50px;

  &:hover {
    text-decoration: none;
  }

  @media screen and (max-width: 991px) {
    font-size: 14px;
  }

  @media screen and (max-width: 768px) {
    font-size: 12px;
    padding: 6px 20px 6px;
  }
}
.secondary-btn-large {
  border-radius: 25px;
  font-weight: bold;
  font-size: 18px;
  color: #1E9AF7;
  border: 2px solid #1E9AF7;
  padding: 9px 50px 10px 50px;

  &:hover {
    text-decoration: none;
  }

  @media screen and (max-width: 991px) {
    font-size: 14px;
  }

  @media screen and (max-width: 768px) {
    font-size: 12px;
    padding: 5px 20px 6px;
  }
}
.primary-btn-small {
  background: #1E9AF7;
  border-radius: 25px;
  font-weight: bold;
  font-size: 14px;
  color: #FFFFFF;
  padding: 7px 27px 8px 27px;

  &:hover {
    text-decoration: none;
  }
}
.secondary-btn-small {
  border-radius: 25px;
  font-weight: bold;
  font-size: 14px;
  color: #133247;
  border: 2px solid #1E9AF7;
  padding: 7px 26px 4px 27px;

  &:hover {
    text-decoration: none;
  }
}
.start-btn-container {
  margin-top: 35px;

  .start-btn {
    color: white;
    font-weight: 600;
    font-size: 16px;
    padding: 10px 35px 10px 35px;
    background-color: #1E9AF7;
    border-radius: 50px;
    text-decoration: none;
    outline: none;
  }
}

/*** tabs ***/
.container--tabs {
  .nav-head {
    height: 90px;
    background: #FFFFFF;
    box-shadow: 0 4px 11px 0 rgba(0,0,0,0.05);
    position: relative;
    width: 100%;

    @media screen and (max-width: 768px){
      height: auto;
    }
  }

  .nav-tabs, .nav-pages {
    float: left;
    margin: 28px 0 0 0;
    list-style-type: none;
    padding-left: 70px;

    @media screen and (max-width: 768px){
      height: auto;
      padding: 0;
    }

    > li {
      float: left;
      margin-top: 0;
      margin-bottom: -1px;

      //@media screen and (max-width: 768px){
      //  float: none;
      //}

      > a {
        float: left;
        margin-right: 60px;
        padding: 0;
        font-size: 17px;
        color: #030303;
        line-height: 28px;

        @media screen and (max-width: 768px){
          margin-right: 12px;
          margin-bottom: 15px;
          font-size: 12px;
        }

        span {
          color: #1E9AF7;
        }

        &:hover {
          text-decoration: none;
        }
      }

      &.active {
        > a,
        > a:hover,
        > a:focus {
          font-weight: bold;
          border-bottom: 2px solid #1E9AF7;
          padding-bottom: 7px;
        }

        @media screen and (max-width: 768px){
          > a,
          > a:hover,
          > a:focus {
            font-weight: bold;
            border-bottom: 2px solid #1E9AF7;
            padding-bottom: 0px;
          }
        }
      }
    }
  }

  .tab-content {
    float: left;
    width: 100%;

    > .tab-pane {
      display: none;

      &.active {
        display: block;
      }
    }
    > .active {
      display: block;
    }
  }
}

/*** lists ***/
.checklist-blue {
