footer.footer {
  background: #133247;
  padding: 40px 70px;
  clear: both;

  @media screen and (max-width: 991px) {
    padding: 40px 0;
  }

  .site-footer-nav {
    .link {
      font-size: 17px;
      color: #FFFFFF;
      line-height: 28px;
      font-weight: bold;

      @media screen and (max-width: 768px) {
        margin-top: 15px;
        font-size: 14px;
        display: block;
      }
    }

    ul {
      list-style: none;
      padding: 0;
      margin: 0;

      &.simple {
        @media screen and (max-width: 768px) {
          margin-top: 15px;
        }
      }

      li {
        padding-left: 0;

        @media screen and (max-width: 768px) {
          margin: 0;
        }

        a {
          font-size: 14px;
          color: #FFFFFF;
        }
      }
    }
  }

  .footer-down {
    padding-top: 20px;
    margin-top: 40px;

    @media screen and (max-width: 768px) {
      display: block !important;
      margin-top: 15px;

      .d-flex {
        display: block !important;
      }
    }

    .social {
      @media screen and (max-width: 768px) {
        width: 100%;
        text-align: center;
        border-bottom: 1px solid #627793;
        padding-bottom: 25px;
        margin-bottom: 20px;
      }

      a {
        margin-right: 20px;
      }
    }

    nav {
      margin-left: 25px;

      @media screen and (max-width: 768px) {
        margin: 0 0 10px 0;
        text-align: center;
      }

      a {
        font-size: 12px;
        color: #FFFFFF;

        &:last-child {
          &:after {
            display: none;
          }
        }

        &:after {
          content: '';
          width: 1px;
          height: 15px;
          background: #fff;
          display: inline-block;
          vertical-align: middle;
          margin: 0 10px;
        }
      }
    }
  }

  .footer-copyright {
    font-size: 12px;
    color: #FFFFFF;

    @media screen and (max-width: 768px) {
      text-align: center;
    }
  }
}