.hero-screen-accessories {
  background: #375270;
  position: relative;

  .hero-content {
    padding-bottom: 0px;

    @media screen and (max-width: 768px) {
      padding-bottom: 34px;
    }
  }

  .bg-img {
    text-align: right;
    // min-height: 510px;
    // margin-top: 40px;

    @media screen and (max-width:1550px) {
      opacity: 0.4;
    }

    img {
      max-width: 100%;
      max-height: 600px;

      @media screen and (max-width:768px) {
        max-height: 300px;
      }
    }
  }

  @media screen and (max-width: 768px) {
    height: 300px;
  }
}

.b-accessories-items {
  @media screen and (max-width: 768px) {
    margin: 20px 0 40px;
  }

  .item {
    max-width: 1030px;
    width: 100%;
    margin: 0 auto 28px;
    background: #FFFFFF;
    box-shadow: 0 10px 18px 0 rgba(50,50,93,0.14);
    border-radius: 9.33px;
    padding: 50px 70px;

    @media screen and (max-width: 768px) {
      padding: 20px 15px;
    }

    p {
      font-size: 16px;
      color: #000000;
      line-height: 28px;
      padding-top: 25px;
    }

    ul {
      margin-bottom: 0;

      li {
        font-size: 16px;
        color: #000000;
        line-height: 35px;

        a {
          &:after {
            content: '';
            width: 20px;
            height: 16px;
            display: inline-block;
            vertical-align: middle;
            margin: -4px 0 0 8px;
            background: url("https://d85ecz8votkqa.cloudfront.net/images/booynt/icons/arrow_ic.svg") no-repeat;
          }
        }
      }
    }

    img {
      display: block;
      margin: 0 auto;
      max-width: 300px;

      @media screen and (max-width: 768px){
        margin: 30px auto;
        max-width: 200px;
      }
    }
  }
}
