.main-screen {
  position: relative;
  box-shadow: 0 5px 7px 0 rgba(0,0,0,0.18);

  .video {
    background-position: right;
    background-size: cover;
    position: relative;

    @media screen and (min-width: 768px) {
      background-image: none !important;
    }

    &:after {
      content: '';
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      background: rgba(0, 0, 0, 0.4);
      z-index: 2;
    }

    @media screen and (max-width: 768px) {
      position: absolute;
      top: 0px;
      right: 0px;
      height: 100%;
      width: 100%;

      &:after {
        background: rgba(0, 0, 0, 0.8);
      }
    }
  }

  video {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 1;

    @media screen and (max-width: 768px) {
      display: none;
    }

    @media screen and (max-width: 1670px) {
      max-height: 1000px;
    }
  }

  .item {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    display: flex;
    align-items: flex-end;
    padding-bottom: 88px;
    z-index: 4;

    @media screen and (max-width: 768px) {
      padding-bottom: 34px;
    }

    .container {
      @media screen and (max-width: 768px) {
        position: absolute;
        width: 100vw;
      }

      @media screen and (max-width: 400px) {
        position: absolute;
        width: 100vw;
      }
    }

    .main-sub-container {
      margin-bottom: 30px;
      margin-left: 55px;
      padding-left: 20px;
      border-left: 2px solid $primary-site-color;
    
      @media screen and (max-width: 768px) {
        margin-left: 0;
      }

      .main-subheader1 {
        margin-top: 0;
        margin-bottom: 0px;
        text-rendering: optimizeLegibility;
        font-size: 20px;
        color: #FFFFFF;
        line-height: 30px;
        font-weight: 600;
  
        @media screen and (max-width: 1024px) {
          font-size: 20px;
          line-height: 25px;
        }
  
        @media screen and (max-width: 768px) {
          font-size: 13px;
          line-height: 18px;
          margin-bottom: 5px;
          padding-left: 0;
        }
      }
      
      .main-subheader2 {
        margin-top: 0;
        margin-bottom: 0px;
        text-rendering: optimizeLegibility;
        font-size: 16px;
        color: #FFFFFF;
        line-height: 30px;
        font-weight: 400;
  
        @media screen and (max-width: 1024px) {
          font-size: 20px;
          line-height: 25px;
        }
  
        @media screen and (max-width: 768px) {
          font-size: 13px;
          line-height: 18px;
          margin-bottom: 5px;
          padding-left: 0;
        }
      }

      .first {
        font-size: 28px;
        margin-bottom: 8px;

        @media screen and (max-width: 1024px) {
          font-size: 20px;
          line-height: 20px;
          margin-bottom: 10px;
          padding-left: 0;
        }

        @media screen and (max-width: 768px) {
          font-size: 18px;
          line-height: 18px;
        }
      }
    }

    .secondary-btn-large {
      margin-left: 55px;
      color: #fff;
      margin-right: 18px;

      @media screen and (max-width: 768px) {
        margin-left: 0;
        margin-right: 5px;
      }
    }
  }
}

.hero {
  margin-top: 45px;

  @media screen and (max-width: 768px) {
    margin-top: 40px;
  }
}

.feature-items {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-top: 20px;

  .feature-item {
    align-items: center;
    text-align: center;
    margin-left: 25px;
    margin-right: 25px;
    flex: 1;

    @media screen and (max-width: 768px) {
      margin-left: 10px;
      margin-right: 10px;
    }

    .fi-img {
      align-items: center;
      margin-left: auto;
      margin-right: auto;
      max-width: 110px;
      max-height: 110px;
      margin-bottom: 12px;

      @media screen and (min-width: 769px) and (max-width: 1024px) {
        width: 90px;
        height: 90px;
        margin-bottom: 10px;
      }

      @media screen and (max-width: 768px) {
        width: 80px;
        height: 80px;
        margin-bottom: 8px;
      }
    }
    .fi-header {
      font-weight: 600;
      font-size: 18px;

      @media screen and (max-width: 768px) {
        font-size: 12px;
        line-height: 18px;
      }
    }
  }
}

.bundles {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;

  @media screen and (max-width: 768px) {
    margin-top: 15px;
    flex-direction: column;
  }

  .bundle {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    padding-top: 30px;
    padding-bottom: 30px;
    border-radius: 10px;
    -webkit-box-shadow: 0px 9px 16px -8px rgba(122,122,122,1);
    -moz-box-shadow: 0px 9px 16px -8px rgba(122,122,122,1);
    box-shadow: 0px 9px 16px -8px rgba(122,122,122,1);
    cursor: pointer;

    @media screen and (max-width: 768px) {
      padding-top: 20px;
      padding-bottom: 20px;
    }

    .bundle-header {
      font-size: 25px;
      line-height: 35px;
      font-weight: 600;

      @media screen and (max-width: 768px) {
        font-size: 18px;
        line-height: 25px;
      }
    }
    .bundle-text {
      margin-top: 8px;
      font-size: 18px;
      font-weight: 600;
      color: white;

      @media screen and (max-width: 768px) {
        margin-top: 3px;
        font-size: 16px;
      }
    }
    .bundle-features {
      margin-top: 5px;
      color: white;

      @media screen and (max-width: 768px) {
        margin-top: 3px;
      }
    }
    .bundle-subheader {
      opacity: .5;
      color: white;
      margin-top: 5px;
      font-size: 14px;

      @media screen and (max-width: 768px) {
        margin-top: 3px;
        font-size: 13px;
      }
    }
  }
  .bundle-advanced {
    background-color: $primary-site-color;
    margin-right: 20px;

    @media screen and (max-width: 768px) {
      margin-bottom: 15px;
      margin-right: 0px;
    }
  }
  .bundle-premium {
    background-color: $fifth-neutral-color;
  }
  .text-advanced {
    color: $fifth-neutral-color;
  }
  .text-premium {
    color: $primary-site-color;
  }
  .text-white {
    color: white;
  }
}

.features-btn {
  background-color: $primary-site-color;
  color: white;
  cursor: pointer;
  width: 100%;
  border: none;
  text-align: center;
  outline: none;
  font-size: 15px;
}
.features-btn:hover {
  font-weight: 500;
  transition: 0.2s ease;
}

.btn-dark-bg {
  background-color: $fifth-neutral-color;
}

.b-review {
  background: #EAF0F3;
  padding: 80px 0 120px;

  @media screen and (max-width: 768px) {
    padding: 40px 0 0;
  }

  .b-review-slider {
    margin-top: 80px;

    @media screen and (max-width: 768px) {
      margin-top: 40px;
    }

    .owl-item {
      background: #fff;
      max-width: 1280px;

      @media screen and (max-width: 768px) {
        background: #FAFAFA;
      }
    }
  }

  .review-text {
    font-size: 20px;
    color: #030303;
    line-height: 35px;
    padding: 40px 40px 10px 10px;

    @media screen and (max-width: 768px) {
      font-size: 14px;
      line-height: 18px;
      padding: 20px;
    }
  }

  .down {
    margin-top: auto;
    margin-bottom: 25px;

    @media screen and (max-width: 768px) {
      margin: 0 20px 30px;
    }
  }

  .owl-dots {
    text-align: center;
    margin-top: 40px;

    @media screen and (max-width: 768px) {
      display: none;
    }

    .owl-dot {
      width: 16px;
      height: 16px;
      border: 2px solid $primary-site-color;
      border-radius: 50%;
      margin: 0 10px;

      &.active {
        background: $primary-site-color;
      }
    }
  }
}

.b-payments-list {
  .b-items-list {
    @media screen and (max-width: 860px) {
      flex-wrap: wrap;

      .item {
        width: 21%;
        margin: 20px 2% 0;

        span {
          font-size: 11px;
        }
      }
    }

    .item {
      margin: 40px 10px 0;
  
      span {
        display: block;
        text-align: center;
        font-size: 14px;
        color: #353535;
        font-weight: 500;
      }
  
      img {
        max-height: 128px;
      }
    }
  }

  .merchants-brands .row, .merchants-processors .row {
    margin-top: 60px;
    margin-bottom: 0px;
    padding-top: 60px;
    border-top: 1px solid #ECECEC;
  
    @media screen and (max-width: 768px) {
      margin-top: 30px;
      padding-top: 30px;
    }
  }
  
  .merchants-brands .row {
    img {
      display: inline-block;
      vertical-align: middle;
      margin: 0 30px;
      margin-bottom: 30px;
      max-height: 50px;
      max-width: 200px;
  
      @media screen and (max-width: 768px) {
        max-height: 40px;
        margin: 0 20px;
        margin-bottom: 20px;
        max-width: 160px;
      }
    }
  }
}


.modal-bundle {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 100; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.6); /* Black w/ opacity */

  .modal-bundle-content {
    position: relative;
    margin-top: 60px;
    margin-left: auto;
    margin-right: auto;
    padding: 30px;
    width: 55%;
    animation-name: slideinright;
    animation-duration: 0.4s;
    border-radius: 10px;

    @media screen and (min-width: 950px) and (max-width: 1200px) {
      width: 75%;
    }
    @media screen and (max-width: 950px) {
      width: 90%;
      margin-top: 30px;
      animation-name: slideinbottom;
    }
    @media screen and (max-width: 768px) {
      width: 100%;
      margin-top: 30px;
      animation-name: slideinbottom;
    }

    @keyframes slideinright {
      from {left: 100px; opacity: 0}
      to {left: 0px; opacity: 1}
    }

    @keyframes slideinbottom {
      from {bottom: -100px; opacity: 0}
      to {bottom: 0px; opacity: 1}
    }

    .modal-bundle-close {
      color: rgba(255, 255, 255, .6);
      font-size: 32px;
      margin-bottom: 10px;
      text-align: right;

      @media screen and (max-width: 768px) {
        margin-right: 4px;
      }
    }
    
    .modal-bundle-close:hover,
    .modal-bundle-close:focus {
      color: rgba(255, 255, 255, 1);
      text-decoration: none;
      cursor: pointer;
    }
  }
}

.bundles-m {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;

  @media screen and (max-width: 768px) {
    width: 200%;
  }

  .bundle-m {
    display: flex;
    flex: 1;
    flex-direction: column;
    align-items: center;
    padding-bottom: 20px;
    border-radius: 10px;
    background-color: white;
    -webkit-box-shadow: 0px 9px 16px -8px rgba(122,122,122,1);
    -moz-box-shadow: 0px 9px 16px -8px rgba(122,122,122,1);
    box-shadow: 0px 9px 16px -8px rgba(122,122,122,1);

    .bundle-color {
      width: 100%;
      height: 20px;
      border-top-left-radius: 10px;
      border-top-right-radius: 10px;
    }
    .advanced-color {
      background-color: $primary-site-color;
    }
    .premium-color {
      background-color: $fifth-neutral-color;
    }
    .bundle-m-header {
      margin-top: 20px;
      font-size: 24px;
      font-weight: 600;
    }
    .bundle-m-text {
      margin-top: 8px;
      font-size: 18px;
      font-weight: 600;
    }
    .bundle-m-features {
      margin-top: 5px;
      font-size: 15px;
      padding: 0 30px;

      .features-bold {
        font-weight: 600;
      }
    }
    .bundle-m-subheader {
      opacity: .5;
      margin-top: 5px;
      font-size: 14px;
    }
  }
  .bundle-m-advanced {
    margin-right: 15px;
  }
  .bundle-m-premium {
    @media screen and (max-width: 768px) {
      margin-right: 30px;
    }
  }
  .text-m-advanced {
    color: $primary-site-color;
  }
  .text-m-premium {
    color: $fifth-neutral-color;
  }
  .text-m-white {
    color: white;
  }
}