.hero-screen-about {
  height: 400px;
  background: #133247;
  text-align: center;

  @media screen and (max-width: 768px){
    height: 300px;
  }

  .item {
    display: flex;
    height: 100%;
    align-items: center;

    h2 {
      font-size: 52px;
      color: #FFFFFF;
      text-align: center;
      line-height: 64px;
      font-weight: 500;
      margin: 0 0 50px 0;

      @media screen and (max-width: 768px){
        font-size: 40px;
        line-height: 50px;
      }
    }

    span {
      color: #fff;
    }

    a {
      color: #fff;
    }
  }
}

.b-our-mission {
  .sub-title {
    font-size: 40px;
    color: #030303;
    text-align: center;
    line-height: 54px;
    display: block;
    font-weight: 500;
    padding-top: 50px;

    @media screen and (max-width: 768px){
      padding-top: 20px;
      font-size: 18px;
      line-height: 26px;
    }
  }

  .list {
    padding-top: 60px;

    @media screen and (max-width: 768px){
      padding-top: 40px;
    }

    img {
      max-width: 215px;
      width: 100%;
      display: block;
      margin: 0 auto;
    }

    span {
      font-size: 20px;
      color: #030303;
      line-height: 33.6px;
      font-weight: bold;
      padding-top: 50px;
      display: block;
      text-align: center;

      @media screen and (max-width: 768px){
        padding: 20px 0 40px;
      }
    }
  }

  .text {
    padding-top: 50px;

    p {
      font-size: 20px;
      color: #030303;
      line-height: 35px;

      @media screen and (max-width: 768px){
        font-size: 16px;
        line-height: 20px;
      }
    }

    span {
      font-size: 17px;
      color: #030303;
      text-align: center;
      line-height: 28px;
      font-weight: 500;
      display: block;
      padding-top: 20px;

      .learn-more-link {
        margin-left: 12px;
      }
    }
  }
}

.b-evolution {
  .list {
    list-style: none;
    padding: 0;
    margin: 0;
    position: relative;

    &:before {
      content: '';
      width: 2px;
      height: calc(100% - 20px);
      position: absolute;
      left: 79px;
      top: 10px;
      background: #1E9AF7;
    }

    li {
      display: flex;
      margin: 0;

      span {
        font-size: 20px;
        color: #1E9AF7;
        text-align: right;
        line-height: 35px;
        font-weight: 500;
        padding-right: 90px;
        position: relative;

        &:after {
          content: '';
          width: 17px;
          height: 17px;
          background: #1E9AF7;
          border-radius: 50%;
          position: absolute;
          right: 55px;
          top: 9px;
        }
      }

      p {
        font-size: 20px;
        color: #375270;
        line-height: 35px;
        font-weight: 500;

        @media screen and (max-width: 768px){
          font-size: 16px;
          line-height: 24px;
        }
      }
    }
  }

  .learn-more {
    margin-top: 50px;
    font-size: 17px;
    color: #030303;
    line-height: 28px;
    font-weight: 500;

    .learn-more-link {
      margin-left: 12px;
    }
  }
}

.b-numbers {
  background: #133247;

  .row {
    padding-bottom: 90px;

    @media screen and (max-width: 768px){
      padding-bottom: 40px;
    }
  }

  span {
    font-size: 96px;
    color: #FFFFFF;
    line-height: 105px;
    text-align: center;
    font-weight: bold;
    padding-top: 80px;
    display: block;

    @media screen and (max-width: 768px){
      font-size: 40px;
      line-height: 40px;
      padding-top: 25px;
    }
  }

  p {
    font-size: 24px;
    color: #FFFFFF;
    line-height: 35px;
    font-weight: bold;
    margin: 0;
    text-align: center;
    padding-top: 24px;
    display: block;

    @media screen and (max-width: 768px) {
      font-size: 16px;
    }
  }
}

.b-investors {
  .row {
    padding-bottom: 40px;
  }

  .logos {
    text-align: center;
    display: flex;
    justify-content: space-between;
    margin-top: 70px;

    > .row {
      width: 100%;
    }

    @media screen and (max-width: 768px){
      display: block;
      margin-top: 40px;
    }

    img {
      max-height: 66px;

      @media screen and (max-width: 768px){
        display: block;
        margin: 0 auto 25px;
      }
    }
  }
}

.b-offices {
  background: #375270;

  @media screen and (max-width: 768px){
    padding: 40px 0;
  }

  h2 {
    font-size: 40px;
    color: #FFFFFF;
    text-align: center;
    line-height: 52px;
    font-weight: bold;
    padding: 0;
    margin: 0;

    @media screen and (max-width: 768px){
      font-size: 20px;
      line-height: 30px;
      margin-bottom: 20px;
    }
  }

  span {
    font-size: 20px;
    color: #FFFFFF;
    line-height: 35px;
    font-weight: bold;
    padding: 80px 0 30px;
    display: block;

    @media screen and (max-width: 768px){
      font-size: 18px;
      line-height: 20px;
      padding: 20px 0;
    }
  }

  p {
    font-size: 20px;
    color: #FFFFFF;
    margin: 0 0 7px;
    padding: 0;

    @media screen and (max-width: 768px){
      font-size: 16px;
      line-height: 20px;
    }
  }
}
