header.header {
  box-shadow: 0 7px 7px 0 rgba(0,0,0,0.18);
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 121px;
  z-index: 10;
  background: white;

  .covid-container {
    height: 45px;
    width: 100%;
    text-align: center;
    background: #17afab;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 600;
  }

  .site-logo {
    height: 40px;

    @media screen and (max-width: 768px) {
      height: 21px;
    }
  }

  .mobile-search {
    display: none;
  }

  .hamburger {
    display: none;
  }

  .main-nav {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .nav-home {
      display: flex;
    }

    .nav-right {
      align-items: center;
      display: flex;
      margin-right: 15px;
      min-height: 76px;

      ul {
        display: flex;
        justify-content: space-between;
        list-style: none;
        margin: 0;
        padding-right: 49px;

        li {
          display: flex;
          margin: 0 0 0 65px;
          padding: 11px 0;
          position: relative;

          .fa {
            margin-left: 5px;
            width: 10px;
            display: inline-block;
          }

          &:hover {
            .nav-menu-dropdown {
              display: block;
            }
          }

          .nav-menu-dropdown {
            display: none;
            position: absolute;
            left: 0;
            top: 45px;
            margin: 0 0 0 -15px;
            z-index: 9;
            background: #fff;
            max-width: 150px;
            padding: 20px 10px 10px 10px;
            box-shadow: 0 10px 18px 0 rgba(50, 50, 93, 0.14);

            ul {
              display: block;
            }

            li {
              width: 150px;
              display: block;
              margin: 0;
              margin-bottom: 12px;
              padding: 0;
            }

            a.nav-current {
              border-bottom: none;
              font-weight: normal;
            }
          }

          @media screen and (max-width: 1400px) {
            margin-left: 20px;
          }

          a {
            font-size: 14px;
            color: #030303;
            padding: 0;
            letter-spacing: 0.5px;
            transition: color 0.2s;
            border-bottom: 2px solid transparent;

            &:hover {
              text-decoration: none;
              color: #808080;
            }

            &.nav-current {
              font-weight: 700;
              border-bottom: 2px solid #1E9AF7;

              &:hover {
                color: #030303;
              }
            }
          }
        }
      }

      .white-outline {
        font-size: 16px;
        margin-left: 10px;

        &.secondary-btn-small {
          background: #fff;
          font-size: 14px;
          padding: 5px 26px 6px 28px;
        }

        &.primary-btn-small {
          font-size: 14px;
        }
      }

      .search-trigger {
        color: #030303;
        font-size: 18px;
        display: flex;
        align-items: center;
        margin-right: 14px;
        width: 50px;

        &:hover {
          text-decoration: none;
        }

        &:before {
          content: '';
          width: 1px;
          height: 35px;
          display: flex;
          background: #353535;
          margin-right: 30px;
        }
      }
    }
  }
}

@media screen and (min-width: 1191px) {
  header.header {
    .main-nav {
      .nav-right {
        display: flex !important;
      }
    }
  }
}

@media screen and (max-width: 1190px) {
  body {
    padding-top: 50px;
  }

  header.header {
    height: 90px;

    .covid-container {
      height: 45px;
    }

    .main-nav {
      height: 45px;

      .mobile-search {
        display: block;
      }

      .hamburger {
        cursor: pointer;
        display: block;
        zoom: 0.5;
        height: 68px;
        margin-right: -13px;
      }

      .nav-right {
        display: none;
        position: absolute;
        right: 0;
        top: 85px;
        background: #fff;
        z-index: 5;
        padding: 20px;
        width: 100%;
        margin-right: 0;

        ul {
          display: block;

          li {
            margin-left: 0;
          }
        }

        .search-trigger {
          display: none;
        }

        .white-outline {
          display: block;
          width: 100%;
          margin: 10px 0 0 0;
        }
      }
    }
  }
}
