.legal-content {
  max-width: 800px;
  padding: 70px 15px;
  margin: 0 auto;

  a {
    color: #0d9ded;
  }
  .legal-compliance-section {
    display: none;
    margin-top: 40px;

    &.active {
      display: block;
    }
  }

  .float-label {
    border-bottom: 1px solid #a6baca;
    display: block;
  }

  select {
    border: none !important;
    border-radius: 0;
    margin-bottom: 12px;
    padding: 4px 6px;
    width: 100%;
    font-size: 14px !important;
    background: #fff;
  }
}

